import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { BrowserModule }  from '@angular/platform-browser';
import { AppComponent } from './../component/App';
import { IndexComponent } from './../component/Index';
import { HeaderComponent } from './../component/Header';
import { ObjectWatcherComponent } from '../component/objectWatcher/Index';
import { ObjectWatcherObjectNodeComponent } from '../component/objectWatcher/ObjectNode';
@NgModule({
    imports: [
        BrowserModule,
        RouterModule.forRoot([
            {
                path: "",
                component: IndexComponent,
                children: [
                    {
                        path: "",
                        component: ObjectWatcherComponent
                    },
                    {
                        path: "objectWatcher",
                        component: ObjectWatcherComponent
                    }
                ]
            }
        ])
    ],
    declarations: [
        AppComponent,
        IndexComponent,
        HeaderComponent,
        ObjectWatcherComponent,
        ObjectWatcherObjectNodeComponent
    ],
    bootstrap: [ AppComponent ]
})
export class AppModule { }



