import { Component, Input } from '@angular/core';
@Component({
    selector: 'vwvo-objectWatcher-objectNode',
    template: require('./../../tmpl/objectWatcher/objectNode.html')
})
export class ObjectWatcherObjectNodeComponent {
    @Input() objectNode:any;
    private toggleChildren(target:any) {
        target = $(target);
        target.parents(".objectNode:first").toggleClass("open");
    }
}


