/// <reference path="./../../util/jquery.d.ts" />
import { Component } from '@angular/core';
@Component({
    selector: 'vwvo-objectWatcher',
    template: require('./../../tmpl/objectWatcher/index.html'),
    styles: [require('./../../../css/objectWatcher/index.css').toString()]
})
export class ObjectWatcherComponent {
    private treeObj:any;
    private oriObj:any;
    private textChange(e:any) {
        if(e.keyCode !== 13) {
            return;
        }
        let text = e.target.value;
        try {
            let obj = JSON.parse(text);
            // transform object
            let rootObj = obj.objects[obj.value.$ref];
            this.oriObj = obj;
            this.treeObj = this.transformObject(rootObj);
        } catch(e) {
            alert(e.message);
        }
    }

    private transformObject(obj:any) {
        if(!obj) {
            return  {
                id: "null",
                isObj: false,
                schema: "",
                value: "null"
            };
        }

        let id = obj.$id;
        let schema = obj.$schema;
        let children:any[] = [];
        let tmpObj:any = {
            id: id,
            schema: schema,
            isObj: true,
            children: children,
            value: id
        };
        for(let key in obj) {
            let value = obj[key];
            if(key == "$id" || key == "$schema") {
                continue;
            }
            if(!value) {
                children.push({
                    key: key,
                    value: value
                });
                continue;
            }
            if(this.isSimpleType(value)) {
                children.push({
                    key: key,
                    value: value
                });
                continue;
            }
            if(this.isArray(value)) {
                children.push({
                    key: key,
                    value: "["+value.join(",")+"]"
                });
                continue;
            }
            if(this.isComplexArray(value)) {
                let complexObj = this.collectionComplexArrayData(value);
                Object.assign(complexObj, {key: key});
                children.push(complexObj);
                continue;
            }
            if(this.isObject(value)) {
                let id = value.$ref;
                // 对象
                children.push(this.transformObject(this.oriObj.objects[id]));
            }
        }

        return tmpObj;
    }

    private collectionComplexArrayData(obj:any) {
        let _obj:any;
        let array:any[] = [];
        let returnJson:any = {};
        let isSimpleType:boolean = false;
        let isArray:boolean = false;
        let isObject:boolean = false;
        for(let key in obj) {
            if(key == "$count") {
                continue;
            }
            _obj = obj[key];
            if(_obj !== null) {
                isSimpleType = this.isSimpleType(_obj);
                isArray = this.isArray(_obj);
                isObject = this.isObject(_obj);
                break;
            }
        }
        if(isSimpleType || isArray || isObject) {
            for(let key in obj) {
                if(key == "$count") {
                    continue;
                }
                _obj = obj[key];
                if(isSimpleType) {
                    array.push(_obj);
                    continue;
                }
                if(isArray) {
                    array.push("["+_obj ? _obj.join(",") : ""+"]");
                    continue;
                }
                if(isObject) {
                    array.push(this.transformObject(_obj ? this.oriObj.objects[_obj.$ref] : null));
                }
            }
        }

        if(isSimpleType || isArray) {
            returnJson.value = "["+array.join(",")+"]";
        } else if(isObject) {
            returnJson.isObj = true;
            returnJson.value = "[object]";
            returnJson.children = array;
        }

        return returnJson;
    }

    private isComplexObj(obj:any) {
        if(!obj) {
            return;
        }

        return (obj && typeof obj == "object");
    }

    private isSimpleType(obj:any) {
        let type = typeof obj;
        return type == "boolean" || type == "string" || type == "number";
    }
    private isArray(obj:any) {
        return $.isArray(obj);
    }
    private isComplexArray(obj:any) {
        return obj.$count;
    }
    private isObject(obj:any) {
        return typeof obj.$ref !== "undefined";
    }
}



